.project_card {
    padding: 1em;
    border: 1px solid #7a7a7a;
    border-radius: 4px;
    width: 24%;
    margin: 0.5%;
}

.project_card h4 {
    background-color: #222;
    color: #ffbb33;
    padding: .4em;
    margin-bottom: 1.3em;
    font-size: 1.3em;
}

.project_card p {
    color: #7a7a7a;
    margin-bottom: 1em;

}

.project_card p span{
    font-weight: bold;

}

.category_text {
    display: flex;
    align-items: center;
}

.category_text span {
    display: block;
}

.category_text span {
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #ccc;
    margin-right: 4px;
}

.category_text .infra {
    background-color: #ffaebc
}
.category_text .desenvolvimento{
    background-color: #a0e7e5;
}
.category_text .design {
    background-color: #b4f8c8;
}
.category_text .planejamento {
    background: color #eec178;;
}

.project_card_actions {
    margin-top: 1.2em;
    display: flex;
    align-items: center;
}

.project_card_actions a, 
.project_card button {
    text-decoration: none;
    border: none;
    background-color: #fff;
    color: #222;
    font-size: .9em;
    padding: .6em 1em;
    cursor: pointer;
    margin-right: 1em;
    border: 1px solid #222;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
}

.project_card_action svg{
    margin-right: 0.5em;
}

.project_card_actions a:hover,
.project_card button:hover {
    background-color: #222;
    color: #ffbb33;
}